* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

.App {
  line-height: 1.6;
  color: #333;
}

nav {
  position: fixed;
  top: 0;
  width: 100%;
  background: #fff;
  padding: 1rem;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  z-index: 1000;
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 2rem;
}

nav a {
  text-decoration: none;
  color: #333;
}

section {
  min-height: 100vh;
  padding: 5rem 2rem 2rem;
  scroll-margin-top: 60px;
}

#welcome {
  background: #f4f4f4;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

h2 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
  border-bottom: 2px solid #333;
  display: inline-block;
}

.experience-item, .project-item {
  margin: 2rem 0;
}
